import { 
    FETCH_QUIZES_FAILURE, 
    FETCH_QUIZES_START, 
    FETCH_QUIZES_SUCCESS,
    FETCH_QUIZ_SUCCESS,
    QUIZ_SET_STATE,
    FINISH_QUIZ,
    QUIZ_NEXT_QUESTION,
    QUIZ_RETRY 
} from "../actions/actionTypes"

const initialState = {
    quizes: [],
    loading: false,
    error: null,
    results: {}, // {[id]: 'success' 'error'}
    activeQuestion: 0,
    isFinished: false,
    answerState: {}, // {[id]: 'success' 'error'}
    quiz: null
}

export default function quizReducer(state = initialState, action) {
    switch(action.type){
        case FETCH_QUIZES_START:
            return {
                ...state, 
                loading: true
            }
        case FETCH_QUIZES_SUCCESS:
            return {
                ...state,
                loading: false,
                quizes: action.quizes
            }
        case FETCH_QUIZES_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            }
        case FETCH_QUIZ_SUCCESS:
            return {
                ...state,
                loading: false,
                quiz: action.quiz
            }
        case QUIZ_SET_STATE:
            return {
                ...state,
                answerState: action.answerState,
                results: action.results 
            }
        case FINISH_QUIZ:
            return {
                ...state,
                isFinished: true
            }
        case QUIZ_NEXT_QUESTION:
            return {
                ...state,
                answerState: null,
                activeQuestion: action.number
            }
        case QUIZ_RETRY:
            return {
                ...state,
                results: {}, 
                activeQuestion: 0,
                isFinished: false,
                answerState: null
            }
        default:
            return state
    }
}


